<template>
  <div
    className="p-fluid p-grid p-justify-center p-ai-center vertical-container p-text-bold"
  >
    <div className="p-col-2"></div>
    <div className="p-col-3 p-text-left">
      <label htmlFor="units" class="p-pl-2">Units</label>
    </div>
    <div className="p-col-3 p-text-left">
      <label htmlFor="price" class="p-pl-2">Price</label>
    </div>
    <div className="p-col-3 p-text-left">
      <label htmlFor="total" class="p-pl-2">Total</label>
    </div>
    <div className="p-col-1"></div>
  </div>
  <template v-for="(item, index) in items" :key="index">
    <!-- <span>{{ item.unit }}</span> -->
    <average-item
      :unit="item.unit"
      :price="item.price"
      :total="item.total"
      :index="index"
      :size="items.length"
      @action="onAction"
      @update="onUpdate"
    ></average-item>
  </template>
  <hr />
  <div
    className="p-fluid p-grid p-justify-center p-ai-center vertical-container p-text-bold"
  >
    <div className="p-col-2"></div>
    <div className="p-col-3 p-text-left">
      <label htmlFor="units" class="p-pl-2">{{ average.unit.toFixed(config.maxFractionDigits) }}</label>
    </div>
    <div className="p-col-3 p-text-left">
      <label htmlFor="price" class="p-pl-2">{{ average.price.toFixed(config.maxFractionDigits) }}</label>
    </div>
    <div className="p-col-3 p-text-left">
      <label htmlFor="total" class="p-pl-2">{{ average.total.toFixed(config.maxFractionDigits) }}</label>
    </div>
    <div className="p-col-1"></div>
  </div>
</template>
<script lang="ts">
import { defineComponent } from 'vue';

import AverageItem from '@/components/AverageItem.vue';
import AverageItemModel from '@/models/AverageItemModel';
import { config, Configuration } from '@/util/config';

export default defineComponent({
  name: 'average-item-list',
  components: {
    AverageItem,
  },
  watch: {
    items: {
      deep: true,
      handler() {
        this.calculateAverage();
      },
    },
  },
  data() {
    return {
      items: [] as AverageItemModel[],
      average: {
        unit: 0,
        price: 0,
        total: 0,
      },
      config: config as Configuration,
    };
  },
  methods: {
    onAction(value: boolean, index: number) {
      if (value) {
        const averageItemModel: AverageItemModel = new AverageItemModel();
        this.items.push(averageItemModel);
      } else {
        const item = this.items[index];

        if (item) {
          this.items.splice(index, 1);
        }
      }
    },
    onUpdate(index: number, value: AverageItemModel) {
      const item = this.items[index];

      if (item) {
        item.unit = value.unit;
        item.price = value.price;
        item.total = value.total;
      }
    },
    calculateAverage() {
      const size: number = this.items.length;

      function sum(items: Array<any>, key: string) {
        return items.reduce((a, b) => a + (b[key] || 0), 0);
      }

      this.average.unit = sum(this.items, 'unit') / size;
      this.average.price = sum(this.items, 'price') / size;
      this.average.total = sum(this.items, 'total') / size;
    },
  },
  mounted() {
    const averageItemModel: AverageItemModel = new AverageItemModel();

    this.items = [averageItemModel];
  },
});
</script>
<style scoped lang="scss"></style>
